<template>
  <div class="home-container">
    <el-scrollbar class="scrollbar-style" :style="{ height: '100vh' }">
      <div class="scrollbar-content-style">
        <header>
          <div class="title-content-box">
            <p class="title-level-one">福建省家庭教育研究会</p>
            <p class="title-level-two">数智化管理平台</p>
          </div>
        </header>
        <!-- 主要内容区域 -->
        <main class="flex align-start">
          <!-- 左侧列表 -->
          <div>
            <ul class="list-style">
              <li class="list-item">
                <ListCard
                  title="近期宣讲区域列表"
                  :listData="recentPreachAreaListCmp"
                >
                  <TableList
                    :columnsList="recentPreachAreaListColumns"
                    :listData="recentPreachAreaListCmp"
                    overflow
                  />
                </ListCard>
              </li>
              <li class="list-item">
                <ListCard title="近期承办单位列表">
                  <TableList
                    :columnsList="recentUndertakingUnitListColumns"
                    :listData="recentUndertakingUnitListCmp"
                    overflow
                  />
                </ListCard>
              </li>
              <li class="list-item">
                <ListCard title="近期宣讲详情">
                  <TableList
                    :columnsList="recentAboutDetailsColumns"
                    :listData="recentAboutDetailsCmp"
                    overflow
                  />
                </ListCard>
              </li>
            </ul>
          </div>

          <!-- 中心地图 -->
          <div class="main-box">
            <TitleCard
              class="title-card-style"
              :title="`宣讲覆盖人数：${coverTotalNumber}人`"
            />
            <MapProjection :dataSource="mapProjection_DataSource" />
          </div>

          <!-- 右侧列表 -->
          <div>
            <ul class="list-style">
              <li class="list-item">
                <ListCard title="宣讲区域覆盖人数排名">
                  <TableList
                    :columnsList="coverListColumns"
                    :listData="coverList"
                    :showHeader="false"
                    overflow
                  />
                </ListCard>
              </li>
              <li class="list-item">
                <ListCard title="授课老师宣讲数据排名">
                  <TableList
                    :columnsList="instructorColumns"
                    :listData="instructorList"
                    overflow
                  />
                </ListCard>
              </li>
              <li class="list-item">
                <ListCard title="新增宣讲覆盖人数">
                  <CoverPeopleTotal
                    :dataSource="coverPeopleTotal_DataSource"
                    :xAxisData="coverPeopleTotal_xAxisData"
                  />
                </ListCard>
              </li>
            </ul>
          </div>
        </main>
      </div>
    </el-scrollbar>
  </div>
</template>

<script>
export default {
  components: {
    ListCard: () => import("./components/ListCard"),
    TableList: () => import("./components/TableList"),
    TitleCard: () => import("./components/TitleCard"),
    CoverPeopleTotal: () => import("./components/CoverPeopleTotal"),
    MapProjection: () => import("./components/MapProjection"),
  },
  computed: {
    /**
     * 近期宣讲区域列表
     */
    recentPreachAreaListCmp() {
      const arr = [];
      for (const item of this.recentList) {
        console.log(
          "区域 :>> ",
          `${item.province}${item.city}${item.district}${item.street}`
        );
        arr.push({
          areaName: `${item.province}${item.city}${item.district}${item.street}`,
          date: this.$dateFormat(item.start_date, "yyyy-mm-dd"),
        });
      }
      return arr;
    },
    /**
     * 近期承办单位列表
     */
    recentUndertakingUnitListCmp() {
      const arr = [];
      this.recentList.forEach((item) => {
        arr.push({
          ...item.host_info,
          date: this.$dateFormat(item.start_date, "yyyy-mm-dd"),
        });
      });
      console.log("近期承办单位列表arr :>> ", arr);
      return arr;
    },
    /**
     * 近期宣讲详情
     */
    recentAboutDetailsCmp() {
      const arr = [];
      this.recentList.forEach((item) => {
        arr.push({
          ...item.instructor_info,
          title: item.title,
          visitor: `${item.visitor}人`,
        });
      });
      console.log("近期宣讲详情arr :>> ", arr);
      return arr;
    },
    /**
     * 新增宣讲覆盖人数-图表数据
     */
    coverPeopleTotal_DataSource() {
      const arr = [];
      this.dateCoverList.forEach((item) => {
        arr.push(item.sum_visitor);
      });
      console.log("新增宣讲覆盖人数-数据arr :>> ", arr);
      return arr;
    },
    /**
     * 新增宣讲覆盖人数-X轴标签
     */
    coverPeopleTotal_xAxisData() {
      const arr = [];
      this.dateCoverList.forEach((item) => {
        arr.push(this.$dateFormat(item.g_date, "m/dd"));
      });
      console.log("新增宣讲覆盖人数-X轴标题arr :>> ", arr);
      return arr;
    },
    /**
     * 地图-图表数据
     */
    mapProjection_DataSource() {
      const arr = [];

      this.dateCoverCityList.forEach((item) => {
        console.log("地图-图表数据item :>> ", item);

        if (item.city === "宁德市") {
          console.log("宁德 :>> ", item);
        }

        if (item.city !== "全省") {
          arr.push({
            name: item.city,
            value: item.sum_visitor,
          });
        }
      });

      console.log("地图-图表数据arr :>> ", arr);

      return arr;
    },
  },
  data() {
    return {
      // 近期宣讲区域列表columns
      recentPreachAreaListColumns: [
        {
          label: "区域名称",
          value: "areaName",
          align: "left",
        },
        {
          label: "日期",
          value: "date",
          width: 60,
        },
      ],
      // 近期承办单位列表columns
      recentUndertakingUnitListColumns: [
        {
          label: "单位名称",
          value: "name",
          align: "left",
        },
        {
          label: "日期",
          value: "date",
          width: 60,
        },
      ],
      // 近期宣讲详情columns
      recentAboutDetailsColumns: [
        {
          label: "宣讲主题",
          value: "title",
          align: "left",
        },
        {
          label: "主讲人",
          value: "name",
          width: 40,
        },
        {
          label: "听课人数",
          value: "visitor",
          width: 50,
          align: "right",
        },
      ],
      // 覆盖列表columns
      coverListColumns: [
        {
          label: "名次",
          value: "rankingName",
          width: 40,
        },
        {
          label: "覆盖区域名称",
          value: "areaName",
          align: "center",
        },
        {
          label: "区域覆盖人数",
          value: "visitorStr",
          width: 55,
          align: "right",
        },
      ],
      // 授课老师宣讲数据排名columns
      instructorColumns: [
        {
          label: "名次",
          value: "rankingName",
        },
        {
          label: "姓名",
          value: "name",
        },
        {
          label: "听课人数",
          value: "visitorStr",
          width: 48,
          align: "right",
        },
        {
          label: "场次",
          value: "roundStr",
          align: "right",
        },
      ],
      // 近期列表
      recentList: [],
      // 覆盖列表
      coverList: [],
      // 授课老师宣讲数据排名
      instructorList: [],
      // 日期覆盖列表
      dateCoverList: [],
      // 日期覆盖列表-市
      dateCoverCityList: [],
      // 覆盖总人数
      coverTotalNumber: 0,
    };
  },
  mounted() {
    this.recentListAPI();
    this.coverListAPI();
    this.instructorListAPI();
    this.dateCoverListAPI();
    this.coverListCityAPI();
    this.coverTotalNumberAPI();
  },
  methods: {
    /**
     * 近期列表API
     */
    async recentListAPI() {
      const res = await this.$request("lecture.recentList");

      console.log("近期列表API_res :>> ", res);

      res.forEach((item) => {
        item.date = this.$dateFormat(item.date, "yyyy-mm-dd");
        console.log("item.date :>> ", item.date);
      });

      this.recentList = res;
    },
    /**
     * 覆盖列表API
     */
    async coverListAPI() {
      const res = await this.$request("lecture.coverList");
      for (const key in res) {
        res[
          key
        ].areaName = `${res[key].city}${res[key].district}${res[key].street}`;
        res[key].rankingName = `第${parseInt(key) + 1}名`;
        res[key].visitorStr = `${res[key].sum_visitor}人`;
      }

      console.log("覆盖列表API_res :>> ", res);
      this.coverList = res;
    },
    /**
     * 授课老师排名列表API
     */
    async instructorListAPI() {
      const res = await this.$request("lecture.instructorList");
      for (const key in res) {
        res[key].rankingName = `第${parseInt(key) + 1}名`;
        res[key].name = res[key].instructor_info.name;
        res[key].visitorStr = `${res[key].sum_visitor}人`;
        res[key].roundStr = `${res[key].host_time}场`;
      }
      console.log("授课老师排名列表API_res :>> ", res);
      this.instructorList = res;
    },
    /**
     * 日期覆盖列表API
     */
    async dateCoverListAPI() {
      const data = {
        type: "none",
      };
      const res = await this.$request("lecture.dateCoverList", data);
      console.log("日期覆盖列表res :>> ", res);
      this.dateCoverList = res;
    },
    /**
     * 覆盖总人数API
     */
    async coverTotalNumberAPI() {
      const res = await this.$request("lecture.coverTotalNumber");
      console.log("覆盖总人数API_res :>> ", res);
      this.coverTotalNumber = res;
    },
    /**
     * 获取覆盖列表-市
     */
    async coverListCityAPI() {
      const res = await this.$request("lecture.coverListCity");
      console.log("res :>> ", res);
      this.dateCoverCityList = res;
    },
  },
};
</script>

<style lang="scss" scoped>
.home-container {
  width: 100vw;
  height: 100vh;
  // padding: px(28) px(39);
  // width: calc(100vw - px(39 * 2));
  // height: calc(100vh - px(28 * 2));
  background: border-box no-repeat center / 100% 100%
    url("~@/assets/images/BG.png");
  position: relative;
  overflow: hidden;

  .scrollbar-style {
    ::v-deep .el-scrollbar__wrap {
      overflow-x: hidden;
    }
    ::v-deep .el-scrollbar__bar {
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
    }

    .scrollbar-content-style {
      padding: px(28) px(39);
      overflow-x: hidden;

      header {
        margin: 0 auto;
        width: px(998);
        height: px(114);
        background: border-box no-repeat center / 100% 100%
          url("~@/assets/images/big_headline_box.png");
        text-align: center;
        .title-content-box {
          padding: px(4) 0 0;
          text-align: center;
          font-family: Alibaba PuHuiTi;
          font-weight: bold;
          color: #b4d3ff;
          letter-spacing: px(10);
          background: linear-gradient(
            0deg,
            #3c87ff 0%,
            #24a9e3 36.1083984375%,
            #86effe 75.3662109375%,
            #a4f3ff 100%
          );
          background-clip: text;
          -webkit-text-fill-color: transparent;
          .title-level-one {
            font-size: px(28);
          }
          .title-level-two {
            font-size: px(22);
            margin: px(8) 0 0 0;
          }
        }
      }
      main {
        margin-top: px(-60);

        .main-box {
          padding: px(70) 0 0;
          width: px(507);
          .title-card-style {
            margin: 0 auto;
          }
        }

        .list-style {
          .list-item {
            margin-bottom: px(10);
          }
        }
      }
    }
  }
}
</style>
